.header {
  height: 70px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin-bottom: 25px;
  position:relative;

  @media screen and (max-width: 900px) {
    height: 60px;
    padding: 10px;
    margin-bottom: 20px;
  }

  .logo-container {
    height: 100%;
    width: 70px;
    padding: 25px;

    @media screen and (max-width: 900px) {
      width: 50px;
      padding: 0px;
    }
  }

  .options {
    width: 50%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-end;

      @media screen and (max-width: 900px) {
        width: 80%;
      }

    .option {
      padding: 10px 15px;
      @media screen and (max-width: 900px) {
        padding: 5px 8px;
      }
    }

    .profile-img {
      height: 30px;
      width: 30px;
      border-radius: 30px;
      cursor: text;
    }
  }
}
